




















/* eslint-disable @typescript-eslint/no-explicit-any */

import Vue from "vue";
import { mapState } from "vuex";

export default Vue.extend({
  name: "tm-card-form",
  props: {
    title: String,
    action: String,
    apiCallEvent: String,
    jsonData: Object,
    inputs: Array,
  },
  computed: {
    ...mapState(["currentUser"]),
  },
  data() {
    return {
      formData: {},
    };
  },
  methods: {
    handleSubmit: function () {
      this.$emit("api-call", this.formData);
      this.formData = {};
    },
  },
});
